import {
    ChonkyActions,
    FileBrowser,
    FileContextMenu,
    FileHelper,
    FileList,
    FileNavbar,
    FileToolbar,
    setChonkyDefaults,
} from 'chonky';
import { ChonkyIconFA } from 'chonky-icon-fontawesome';
import PropTypes from 'prop-types';
import React from 'react';
import { useState, useEffect } from 'react';

setChonkyDefaults({
  iconComponent: ChonkyIconFA,
  disableDragAndDrop: true
});

export default function FilePicker(props) {
  // get data
  const {
    id,
    multiple_selection_allowed : multipleSelectionAllowed,
    ...otherProps
  } = props;

  const file_map = props.file_map ?? { rootFolderId : "none", fileMap : {} };
  const fileMap = file_map.fileMap;
  console.log("fileMap", Object.keys(fileMap).length);

  // get the setProps function, which is used to communicate
  // changes in the props back to Dash
  // setProps will not be defined unless this component is
  // running in Dash, so use a dummy function in standalone mode
  let setProps;
  if ( props["setProps"] ) {
    setProps = props["setProps"]
  }
  else {
    setProps = () => {}
  }

  // determine the path of the current folder relative
  // to the root of the tree
  function getFolderChain(currentFolderId) {
    const currentFolder = fileMap[currentFolderId];
    const folderChain = [currentFolder];

    let parentId = currentFolder.parentId;
    while (parentId) {
      const parentFile = fileMap[parentId];
      if (parentFile) {
        folderChain.unshift(parentFile);
        parentId = parentFile.parentId;
      } else {
        parentId = null;
      }
    }

    return folderChain;
  }

  // the id_to_refresh prop will be sent to Dash to tell it
  // we need a new fileMap
  const [idToRefresh, setIdToRefresh] = useState('');
  useEffect(() => {
    // only refresh if there is a valid id
    if ( idToRefresh ) {
      console.log("requested to refresh", data.payload.file.id);
      setProps({ "id_to_refresh" : idToRefresh });
    }
  }, [idToRefresh]);
  
  // currentFolderId is the current root (rather than the actual
  // root of the whole tree)
  const [currentFolderId, setCurrentFolderId] = useState(file_map.rootFolderId);
  function changeDirectory(id) {
    console.log("setting root folder id to", id);
    setProps({ "root_folder_id" : id });
    setCurrentFolderId(id);
  }

  // determine which files are in the current folder
  const currentFolder = fileMap[currentFolderId];
  const files = currentFolder.childrenIds
      ? currentFolder.childrenIds.map((fileId) => fileMap[fileId] ?? null)
      : [];

  //console.log("files", files);
  const folderChain = getFolderChain(currentFolderId);

  function handleFileAction(data) {
    // when a file is clicked on...
    if ( data.id === ChonkyActions.MouseClickFile.id ) {
      const clickType = data.payload.clickType;
      const currentId = data.payload.file.id || "none";
      const currentIdArray = [ currentId ];
      console.log(">", clickType, "click");
      if ( currentId != "none" ) {
        // if this is a directory, refresh it
        if ( FileHelper.isDirectory(currentId) ) {
          console.log("requested to update", id) 
          setIdToRefresh(currentId);
        }
      }
    }

    // when a directory is opened...
    if (data.id === ChonkyActions.OpenFiles.id) {
      const { targetFile, files } = data.payload;
      const fileToOpen = targetFile ?? files[0];
      console.log("payload", data.payload);
      if (fileToOpen && FileHelper.isDirectory(fileToOpen)) {
        console.log("> open file", targetFile);
        changeDirectory(fileToOpen);
      }
    }
  }
      
      // if (fileToOpen && FileHelper.isDirectory(fileToOpen)) {
      //   setProps({ "id_to_refresh" : fileToOpen.id })
      //   console.log(">>> opening", fileToOpen.id)
      //   setCurrentFolderId(fileToOpen.id);
        //setProps({ "id_to_refresh" : fileToOpen.id });

    //   // if the component is in single selection mode,
    //   // then update the current selection
    //   const currentFile = data.payload.file.id || "none";
    //   const currentFileArray = [ currentFile ];
    //   if (!multipleSelectionAllowed) {
    //     setProps({ "selected_files" : currentFileArray});
    //     console.log("current single selection:", currentFile);
    //   }

    //   // set this file to be re-explored on the backend
    //   if (currentFile != "none") {
    //     console.log("requested to update", data.payload.file.id)
    //     setProps({ "id_to_refresh" : data.payload.file.id })
    //   }
    // }

    // // if the user opens a folder, change the root folder internally
    // if (data.id === ChonkyActions.OpenFiles.id) {
    //   const { targetFile, files } = data.payload;
    //   console.log(data.payload);
    //   const fileToOpen = targetFile ?? files[0];
    //   if (fileToOpen && FileHelper.isDirectory(fileToOpen)) {
    //     setProps({ "id_to_refresh" : fileToOpen.id })
    //     console.log(">>> opening", fileToOpen.id)
    //     setCurrentFolderId(fileToOpen.id);
    //     //setProps({ "id_to_refresh" : fileToOpen.id });
    //   }
    // }
    // // if we are in multiple selection mode and the selection changes,
    // // then update the current selection
    // if (multipleSelectionAllowed && 
    //     data.id === ChonkyActions.ChangeSelection.id) {
    //   let selectedFiles = data.payload.selection;
    //   selectedFiles = Array.from(selectedFiles);
    //   setProps({ "selected_files" : selectedFiles });
    //   console.log("current multiple selection:", selectedFiles);
    // }



  const actionsToDisable = [ ChonkyActions.OpenSelection.id ]
  if (!multipleSelectionAllowed) {
    actionsToDisable.push(ChonkyActions.SelectAllFiles.id);
    actionsToDisable.push(ChonkyActions.ClearSelection.id);
  }

  return (
      <FileBrowser
        files={files}
        folderChain={folderChain}
        onFileAction={handleFileAction}
        defaultFileViewActionId={ChonkyActions.EnableListView.id}
        disableDefaultFileActions={actionsToDisable}
        disableSelection={!multipleSelectionAllowed}
      >
        <FileNavbar />
        <FileToolbar />
        <FileList />
      </FileBrowser>
  );
};

FilePicker.defaultProps = {
    selected_files : [],
    file_map : {},
    id_to_refresh : '',
    root_folder_id : '',
    multiple_selection_allowed : true,
};

FilePicker.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * Whether we are allowed to select multiple files
     */
    multiple_selection_allowed: PropTypes.bool,

    /**
     * Selected files.
     */
    selected_files: PropTypes.array,

    /**
     * When this id changes, Dash should explore the given id.
     */
    id_to_refresh: PropTypes.string,

    /**
     * The id of the root folder.
     */
    root_folder_id: PropTypes.string,

    /**
     * JSON-style dictionary containing file tree.
     */
    file_map: PropTypes.object,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};
